import React from 'react';
import { graphql } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';

import Layout from '../../components/Layout';
import Banner from '../../components/Banner';
import Container from '../../components/Container';
import Footer from '../../components/Footer';
import SpaceDivider from '../../components/SpaceDivider';
import BlockHeaderNoImage from '../../components/BlockHeaderNoImage';
import BlockCaseStudyCards from '../../components/BlockCaseStudyCards';
import BlockLogos from '../../components/BlockLogos';
import BlockCTA from '../../components/BlockCTA';

import { edgesByName } from '../../utils/gql';
import { fonts, colors, screen } from '../../constants';

export const pageQuery = graphql`
  {
    sdiFireImage: file(name: { eq: "sdi-fire-product" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    hyperikonImage: file(name: { eq: "hyperikon-warehouse" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    site {
      siteMetadata {
        siteUrl
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 150) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
  }
`;
export default function caseStudiesPage({ data }) {
  const title = 'Ð¡ustomer Success Stories';
  const description =
    'Learn how our customers have used Resolve to simplify their accounts receivable, improve cash flow, reduce risk, and grow sales.';
  const companies = edgesByName(data.companies, node => node.image.fluid);

  return (
    <Layout>
      <GatsbySeo
        title={title}
        description={description}
        openGraph={{
          url: `${data.site.siteMetadata.siteUrl}/customers/`,
          title: `${title} | Resolve`,
          description: `${description}`,
        }}
      />
      <Banner />
      <Container>
        <BlockHeaderNoImage
          pretitle="CUSTOMER STORIES"
          title="Our successful customers, commercial suppliers & sellers"
          desc="Join innovative companies in choosing Resolve to simplify their accounts receivable, improve cash flow, reduce risk, and grow sales."
        />
        <BlockCaseStudyCards
          items={[
            {
              title: 'SDI Fire: Safety Equipment Supplier',
              description: (
                <>
                  <span>
                    SDi Fire provides security and fire alarm testing equipment
                    to corporations all around the world. SDi Fire now offers a
                    wide array of industrial grade fire alarm testing products
                    from top manufacturers all around the world.
                  </span>
                  <br />
                  <br />
                  <span>
                    Since partnering with Resolve, SDi Fire has seen improvement
                    on their profit margins. Their profit margins have increased
                    by 25% in comparison to prior partnering with Resolve.
                  </span>
                </>
              ),
              link: '/customers/sdi-fire/',
              image: data.sdiFireImage.childImageSharp.fluid,
              additionalInfo: (
                <div className="data-info">
                  <div>
                    <div className="red-title">25%</div>
                    Profit Margin Lift
                  </div>
                  <div>
                    <div className="red-title">2 weeks</div>
                    Reduced time for <br />
                    new credit approvals
                  </div>
                </div>
              ),
            },
            {
              title: 'Hyperikon: Commercial LED Seller',
              description: (
                <>
                  <span>
                    Hyperikon is a US manufacturer and seller of commercial LED
                    lights, serving a vast community of business customers and
                    electrical professionals.
                  </span>
                  <br />
                  <br />
                  <span>
                    Since partnering with Resolve, Hyperikon are truly able to
                    provide a customer benefit, net terms encourages larger
                    purchases and higher repeat purchase rates. "Our close rate
                    is easily 50% higher because we offer Resolve."
                  </span>
                </>
              ),
              link: '/customers/hyperikon/',
              image: data.hyperikonImage.childImageSharp.fluid,
              additionalInfo: (
                <div className="data-info">
                  <div>
                    <div className="red-title">3x</div>
                    Average order size
                  </div>
                  <div>
                    <div className="red-title">90%</div>
                    Faster sales cycle
                  </div>
                  <div>
                    <div className="red-title">1.5x</div>
                    More closed b2b sales
                  </div>
                </div>
              ),
            },
          ]}
          showCTA={false}
        />
        <SpaceDivider height={100} />
        <BlockLogos
          title={
            'We already manage the net terms for thousands of customers from these innovative companies'
          }
          data={[
            {
              image: companies['Vice Golf'],
              name: 'Vice Golf',
              offsetTop: 0,
            },
            {
              image: companies['Article'],
              name: 'Article',
            },
            {
              image: companies['OnDemand Tires'],
              name: 'OnDemand Tires',
              offsetTop: 0,
            },
            {
              image: companies['Archipelago Lighting'],
              name: 'Archipelago Lighting',
              offsetTop: 0,
            },
            {
              image: companies['Linus Bike'],
              name: 'Linus Bike',
              offsetTop: 0,
            },
            {
              image: companies['QuietKat'],
              name: 'QuietKat',
              offsetTop: 0,
            },
          ]}
        />
        <SpaceDivider height={100} />
        <BlockCTA
          pretitle="See Resolve for yourself."
          title={
            <span>
              Ready to offer
              <br /> smarter net terms?
            </span>
          }
          primaryButtonText="Request demo"
          primaryButtonTo="/contact-sales/"
        />
      </Container>
      <Footer />
      <style jsx>{`
        .data-info {
          display: flex;
          font-family: ${fonts.halyardDisplay};
          font-style: normal;
          font-weight: 600;
          font-size: 16px;
          line-height: 1.4;
          letter-spacing: 0.024px;
          text-transform: uppercase;
          margin-top: 32px;
          margin-bottom: 64px;
          max-width: 540px;
        }

        .red-title {
          text-transform: none;
          font-size: 48px;
          line-height: 1.2;
          letter-spacing: 0.053px;
          color: ${colors.salmon};
          padding-bottom: 8px;
        }

        .data-info > div:not(:last-child) {
          padding-right: 24px;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .data-info {
            flex-direction: column;
          }

          .data-info > div:not(:last-child) {
            margin-bottom: 22px;
            padding-right: 0;
          }
        }
      `}</style>
    </Layout>
  );
}
