import React from 'react';
import { navigate } from 'gatsby';
import Img from 'gatsby-image';

import { fonts } from '../constants';
import Button from './Button';
import './fonts.css';

export default function CaseStudyCard({
  title,
  description,
  image,
  link,
  additionalInfo,
}) {
  function handleNavigateTo() {
    navigate(link);
  }

  return (
    <div className="case-study-card">
      {image && (
        <Img
          fluid={image}
          imgStyle={{ objectFit: 'cover' }}
          alt={title}
          className="case-study-card__image"
        />
      )}
      <div className="case-study-card__title">{title}</div>
      <div className="case-study-card__description">{description}</div>
      {additionalInfo}
      <div className="case-study-card__button">
        <Button onClick={handleNavigateTo} primary>
          Read more
        </Button>
      </div>

      <style jsx>{`
        .case-study-card__title {
          font-family: ${fonts.halyardText};
          font-weight: 400;
          padding: 24px 0;
          font-size: 32px;
          line-height: 1.2;
        }
        .case-study-card__description {
          font-family: ${fonts.halyardDisplay};
          font-size: 20px;
          font-weight: 400;
        }
        .case-study-card__button {
          margin-top: 32px;
        }
      `}</style>
      <style jsx global>{`
        .case-study-card__image {
          height: 195px;
        }
      `}</style>
    </div>
  );
}
